<template>
  <div id="app">
    <img :src="ainBg" class="ain-bg">
    <div class="load-wrapper" v-show="isLoadShow">
      load...
    </div>
    <router-view/>
  </div>
</template>

<script>
import loadImages from 'common/js/loadImages'
export default {
  name: 'App',
  data () {
    return {
      isLoadShow: true,
      ainBg: './static/img/1 (1).jpg'
    }
  },
  created () {
    let _this = this
    loadImages.loadImages({
      data: _this.resource.getResArr(),
      step (num) {
        console.log(num)
        if (num === 100) {
          _this.isLoadShow = false
          _this.frameAnim()
        }
      },
      compelte () {
        console.log('加载结束')
      },
      needOneStep: true,
      path: './static/img/'
    })
    // 获取用户信息
    // http://p.gerinn.com/shellvideo/index.php/shell/userInfo/
    this.$http.get('http://p.gerinn.com/shellvideo/index.php/shell/userInfo/').then((response) => {
      if (response.data.ret === 1) {
        this.globle.userInfo = response.data.data
      }
    }).catch((error) => {
      console.log(error)
    })    
  },
  methods: {
    frameAnim () {
      let num = 1
      setInterval(() => {
        this.ainBg = this.resource.getRes('bg' + num)
        num++
        if (num === 60) {
          num = 1
        }
      }, 60)
    }
  }
}
</script>

<style>
.load-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: auto;
  background: #fff;
  z-index: 99999;
  text-align: center;
  font-size: 0.3125rem;
}
.ain-bg {
  position: absolute;
  top: 0;
  left: 0;
  margin: auto;
  z-index: 9;
  width: 100%;
  height: 100%;
}
</style>



