<template>
  <div id="swap-container">
    <div class="video-wrapper">
      <div class="full-page-slide-wrapper">
        <div class="slide" ref="slide">
          <div class="slide-group" ref="slideGroup">
            <div v-for="item in items" :key="item.id">
              <div class="full-page-wrapper">
                <video @click="palyVideo($event)" :src="item.video" loop playsinline webkit-playsinline x5-playsinline :poster="item.poster"></video>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <img src="../../common/img/left.png" class="switch-btn left-btn" @click="slidePrev()">
    <img src="../../common/img/right.png" class="switch-btn right-btn" @click="slideNext()">
    <div class="record-wrap">
      <img src="../../common/img/startlook.png" class="record-btn-img">
      <input type="file" class="video-file" id="video-file" accept="video/*" @change="changeVideo($event)">
    </div>
    <div class="jump-page" @click="goRankPage()">跳过</div>
    <div class="tip-overlay" v-show="isShowTip" @click="closeTip()">
    </div>
  </div>
</template>
<script>
import { addClass } from 'common/js/dom'
import BScroll from 'better-scroll'
export default {
  name: 'swap',
  data () {
    return {
      isShowTip: window.localStorage.getItem('flag') ? false : true,
      items: [
        {video: 'http://szp.h5ome.com/qp1.mp4', poster: 'http://szp.h5ome.com/qp1.JPG'},
        {video: 'http://szp.h5ome.com/qp2.mp4', poster: 'http://szp.h5ome.com/qp2.JPG'},
        {video: 'http://szp.h5ome.com/qp3.mp4', poster: 'http://szp.h5ome.com/qp3.JPG'},
        {video: 'http://szp.h5ome.com/qp4.mp4', poster: 'http://szp.h5ome.com/qp4.JPG'}
      ],
      currentPageIndex: 0,
      autoPlay: false,
      interval: 1000,
      ramdomName: '',
      videoSize: '',
      maxSzie: 100 * 1024 * 1024
    }
  },
  created () {
  },
  mounted () {
    this.update()
    window.addEventListener('resize', () => {
      if (!this.slide || !this.slide.enabled) {
        return
      }
      clearTimeout(this.resizeTimer)
      this.resizeTimer = setTimeout(() => {
        if (this.slide.isInTransition) {
          this._onScrollEnd()
        } else {
          if (this.autoPlay) {
            this._play()
          }
        }
        this.refresh()
      }, 60)
    })
  },
  activated() {
    if (!this.slide) {
      return
    }
    this.slide.enable()
    let pageIndex = this.slide.getCurrentPage().pageY
    this.slide.goToPage(pageIndex, 0, 0)
    this.currentPageIndex = pageIndex
    if (this.autoPlay) {
      this._play()
    }
  },
  deactivated() {
    this.slide.disable()
    clearTimeout(this.timer)
  },
  beforeDestroy() {
    this.slide.disable()
    clearTimeout(this.timer)
  },
  methods: {
    closeTip () {
      window.localStorage.setItem('flag', true)
      this.isShowTip = false
    },
    update() {
      if (this.slide) {
        this.slide.destroy()
      }
      this.$nextTick(() => {
        this.init()
      })
    },
    refresh() {
      this._setSlideWidth(true)
      this.slide.refresh()
    },
    next() {
      this.slide.next()
    },
    init() {
      clearTimeout(this.timer)
      this.currentPageIndex = 0
      this._setSlideWidth()
      this._initSlide()

      if (this.autoPlay) {
        this._play()
      }
    },
    _setSlideHeight(isResize) {
      this.children = this.$refs.slideGroup.children

      let height = 0
      let slideHeight = this.$refs.slide.clientHeight
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        addClass(child, 'slide-item')

        child.style.height = slideHeight + 'px'
        height += slideHeight
      }
      if (this.loop && !isResize) {
        height += 2 * slideHeight
      }
      this.$refs.slideGroup.style.height = height + 'px'
    },
    _setSlideWidth(isResize) {
      this.children = this.$refs.slideGroup.children

      let width = 0
      let slideWidth = this.$refs.slide.clientWidth
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        addClass(child, 'slide-item')

        child.style.width = slideWidth + 'px'
        width += slideWidth
      }
      if (this.loop && !isResize) {
        width += 2 * slideWidth
      }
      this.$refs.slideGroup.style.width = width + 'px'
    },    
    _initSlide() {
      this.slide = new BScroll(this.$refs.slide, {
        scrollX: true,
        scrollY: false,
        momentum: false,
        snap: {
          loop: false,
          threshold: 0.3,
          speed: 400
          // easing: {
          //   style: 'cubic-bezier(0.175, 0.885, 0.32, 1.275)'
          // }
        },
        bounce: false,
        click: true
      })
      this.$nextTick(() => {
        document.querySelectorAll('video')[0].play()
        document.addEventListener('WeixinJSBridgeReady', () => {
          for (let i = 0; i < document.querySelectorAll('video').length; i++) {
            document.querySelectorAll('video')[i].play()
            document.querySelectorAll('video')[i].pause()
          }
          document.querySelectorAll('video')[0].play()
        })
      })

      this.slide.on('scrollEnd', this._onScrollEnd)

      this.slide.on('touchEnd', () => {
        if (this.autoPlay) {
          this._play()
        }
      })

      this.slide.on('beforeScrollStart', () => {
        if (this.autoPlay) {
          clearTimeout(this.timer)
        }
      })
    },
    _onScrollEnd() {
      let pageIndex = this.slide.getCurrentPage().pageX
      if (this.currentPageIndex > pageIndex) {
        this.currentPageIndex = pageIndex
        document.querySelectorAll('video')[this.currentPageIndex + 1].pause()
        // document.querySelectorAll('video')[this.currentPageIndex + 1].currentTime  = 0
        document.querySelectorAll('video')[this.currentPageIndex].play()
        // document.addEventListener('WeixinJSBridgeReady', () => {
        //   document.querySelectorAll('video')[this.currentPageIndex + 1].pause()
        //   document.querySelectorAll('video')[this.currentPageIndex].play()
        // })
      } else {
        this.currentPageIndex = pageIndex
        document.querySelectorAll('video')[this.currentPageIndex - 1].pause()
        // document.querySelectorAll('video')[this.currentPageIndex - 1].currentTime = 0
        document.querySelectorAll('video')[this.currentPageIndex].play()
        // document.addEventListener('WeixinJSBridgeReady', () => {
        //   document.querySelectorAll('video')[this.currentPageIndex - 1].pause()
        //   document.querySelectorAll('video')[this.currentPageIndex].play()
        // })
      }
      if (this.autoPlay) {
        this._play()
      }
    },
    _play() {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.slide.next()
      }, this.interval)
    },
    palyVideo (event) {
      event.target.play()
    },
    slidePrev () {
      this.slide.prev()
    },
    slideNext () {
      this.slide.next()
    },
    changeVideo (event) {
      let files = this.globle.videoFile = event.target.files[0]
      this.ramdomName = Math.random().toString(36).substr(2) + files.name.match(/\.?[^.\/]+$/)
      this.videoSize = files.size
      let URL = window.URL || window.webkitURL
      let fileURL = URL.createObjectURL(files)
      window.localStorage.setItem('fileURL', fileURL)
      this.$router.push('/upload')
    },
    goRankPage () {
      this.$router.push('/rank')
    }
  }
}
</script>

<style>
#swap-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}
#swap-container .video-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 1rem;
  width: 7.8125rem;
  height: 12.984375rem;
  background: url(../../common/img/frame.png) no-repeat;
  background-size: 100% 100%;
  margin: auto;
}
#swap-container .switch-btn {
  position: absolute;
  top: 8rem;
  width: 0.578125rem;
}
#swap-container .switch-btn.left-btn {
  left: 0.5rem;
}
#swap-container .switch-btn.right-btn {
  right: 0.5rem;
}
#swap-container .record-wrap {
  position: absolute;
  bottom: 0.5rem;
  left: 0;
  width: 100%;
  height: 1.421875rem;
}
#swap-container .record-wrap img.record-btn-img {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 3.796875rem;
  height: 1.421875rem;
  z-index: 98;
}
#swap-container .record-wrap  .video-file {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 3.796875rem;
  height: 1.421875rem;
  z-index: 99;
  opacity: 0;
}
#swap-container .full-page-slide-wrapper {
  position: absolute;
  z-index: 20;
  top: .8rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 6.5625rem;
  height: 11.6875rem;
  overflow: hidden;
}
#swap-container .full-page-wrapper {
  position: relative;
  left: 0;
  top: 0;
}
#swap-container .full-page-wrapper video {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 6.5625rem;
  height: 11.6875rem;
  object-fit: fill;
  z-index: 9;
}
#swap-container .full-page-wrapper .cover {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  z-index: 10;
}
#swap-container .full-page-slide-wrapper .slide {
  height: 100%;
  min-height: 1px;
}
#swap-container .full-page-slide-wrapper .slide .slide-group {
  height: 100%;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}
#swap-container .full-page-slide-wrapper .slide .slide-group .slide-item {
  float: left;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
#swap-container .full-page-slide-wrapper .slide .slide-group .slide-item .full-page-img-wrapper {
  position: relative;
  height: 100%;
  background-size: cover;
  transform: translateZ(0);
}
#swap-container .tip-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../common/img/leftrighttipoverlay.png) no-repeat;
  background-size: 100% 100%;
  z-index: 9999;
}
#swap-container .jump-page {
  position: absolute;
  top: .2rem;
  right: 1rem;
  width: 2rem;
  height: .8rem;
  line-height: .8rem;
  border: 0.015625rem solid #fff;
  color: #fff;
  font-size: .4rem;
  text-align: center;
}
</style>



