<template>
  <div id="rank-container">
    <div class="video-wrapper">
      <div class="full-page-slide-wrapper">
        <scroll ref="suggest"
                class="suggest"
                :data="result"
                :pullup="pullup"
                :beforeScroll="beforeScroll"
                @scrollToEnd="searchMore"
                @beforeScroll="listScroll">
          <div class="full-page-wrapper clearfix">
            <div class="video-item" v-for="(item, index) in result" :key="item.id" :style="{background: 'url('+ item.vurl +'.jpg) no-repeat 100% / 100%'}" @click="goWatch(index)"></div>           
          </div>
          <loading v-show="hasMore" title="" v-if="isShowLoad"></loading>           
          <div v-show="!hasMore" class="no-result-wrapper">
            <no-result :title="noTipText"></no-result>
          </div>          
        </scroll>
      </div>
    </div>
    <div class="tip-overlay" v-show="isShowTip" @click="closeTip()"></div>
    <div class="ft-btn yaoqing-btn" @click="isShowSharetip = true"></div>
    <div class="ft-btn lookvideo-btn" @click="goMyvideo()"></div>
    <div class="share-overlay" v-show="isShowSharetip" @click="isShowSharetip = false">
      <img src="../../common/img/share-tip.png" class="share-tip">
    </div>
  </div>
</template>

<script>
import Scroll from 'components/scroll/scroll'
import Loading from 'components/loading/loading'
import NoResult from 'components/no-result/no-result'
export default {
  name: 'Rank',
  data () {
    return {
      pullup: true,
      beforeScroll: true,
      hasMore: true,      
      isShowTip: window.localStorage.getItem('flagrank') ? false : true,
      isShowSharetip: false,
      result: [],
      page: 0,
      count: 2,
      hasMore: true,
      isShowLoad: true,
      noTipText: '抱歉，暂无内容'
    }
  },
  created () {
    this.getRank(this.page, this.count) 
  },
  mounted () {
  },
  methods: {
    getRank () {
        this.isShowLoad = true
        if (!this.hasMore) {
          this.isShowLoad = false          
          return
        }      
      // 获取排行榜
      this.$http.get('http://p.gerinn.com/shellvideo/index.php/shell/rankList/'+ this.page +'/'+ this.count).then((response) => {
        console.log(response.data)
        if (response.data.ret === 1) {
          this.globle.getUserInfo = this.result = this.result.concat(response.data.data)
          this.isShowLoad = false
          if (!response.data.data.length) {
            this.hasMore = false
            this.noTipText = '没有更多内容'
          }
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    closeTip () {
      window.localStorage.setItem('flagrank', true)
      this.isShowTip = false
    },
    searchMore () {
      console.log('更多')
      this.getRank(this.page++, this.count)
    },
    listScroll () {
      this.$emit('listScroll')      
    },
    goWatch (index) {
      this.$router.push({path: 'watch', query: {index: index}})      
    },
    goMyvideo () {
      this.$router.push('/myvideo')      
    }
  },
  components: {
    Scroll,
    Loading,
    NoResult
  }
}
</script>

<style>
#rank-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
#rank-container .video-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 0.5rem;
  width: 7.8125rem;
  height: 12.984375rem;
  background: url(../../common/img/frame.png) no-repeat;
  background-size: 100% 100%;
  margin: auto;
}
#rank-container .full-page-slide-wrapper {
  position: absolute;
  z-index: 20;
  top: .9rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 6.6rem;
  height: 11.5rem;
  overflow: hidden;
}
#rank-container .full-page-wrapper {
  position: relative;
  left: 0;
  top: 0;
}
/*#rank-container .full-page-wrapper video {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 6.6rem;
  height: 11.5rem;
  object-fit: fill;
  z-index: 9;
}*/
#rank-container .full-page-wrapper .cover {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  z-index: 10;
}
#rank-container .full-page-slide-wrapper .slide {
  height: 100%;
  min-height: 1px;
}
#rank-container .full-page-slide-wrapper .slide .slide-group {
  height: 100%;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}
#rank-container .full-page-slide-wrapper .slide .slide-group .slide-item {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
#rank-container .full-page-slide-wrapper .slide .slide-group .slide-item .full-page-img-wrapper {
  position: relative;
  height: 100%;
  background-size: cover;
  transform: translateZ(0);
  font-size: 0;
}
#rank-container .full-page-slide-wrapper .video-item {
  float: left;
  width: 3.25rem;
  height: 5.75rem;
}
#rank-container .tip-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../common/img/tipoverlay.png) no-repeat;
  background-size: 100% 100%;
  z-index: 9999;
}
#rank-container .ft-btn {
  position: absolute;
  bottom: .5rem;
  width: 4.0rem;
  height: 1.359375rem;
}
#rank-container .ft-btn.yaoqing-btn {
  left: 1rem;
  background: url(../../common/img/yaoqing.png) no-repeat;
  background-size: 100% 100%
}
#rank-container .ft-btn.lookvideo-btn {
  right: 1rem;
  background: url(../../common/img/lookvideo.png) no-repeat;
  background-size: 100% 100%  
}
#rank-container .share-overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 28, 58, .6);
  z-index: 9999;
}
#rank-container .share-overlay .share-tip {
  width: 3.84375rem;
  position: absolute;
  top: .5rem;
  right: 1rem;
}
.suggest {
  height: 100%;
  overflow: hidden;  
}
.no-result-wrapper {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
</style>



