<template>
  <div id="upload-container">
    <div class="video-wrapper">
      <div class="full-page-slide-wrapper">
        <div class="full-page-wrapper">
          <video :src="fileURL" loop playsinline webkit-playsinline x5-playsinline></video>
        </div>
      </div>
    </div>
    <img src="../../common/img/recode-btn.png" class="f-btn recode-btn">
    <input type="file" class="video-file" accept="video/*" @change="recode($event)">
    <img src="../../common/img/upload-btn.png" class="f-btn upload-btn" @click="uploadVideo()">
    <div class="tip-wrapper" v-show="isShowTip">
      <div class="tip-content">
        <img src="../../common/img/close.png" class="close" @click="isShowTip = false">
        <img src="../../common/img/tip-title.png" class="tip-title">
        <p>感谢您参与“我为壳牌代言”活动，,<br>上传视频即可获得10积分，<br>积分奖励可在公众平台个人账户查看哦~</p>
      </div>
    </div>
    <div class="updata-overlay" v-show="isShowUpOverlay">
        <div id="showtip" v-html="upText">
        </div>    
    </div>
  </div>
</template>

<script>
export default {
  name: 'upload',
  data () {
    return {
      isShowTip: false,
      fileURL: '',
      token: '',
      data: null,
      isShowUpOverlay: false,
      upText: ''
    }
  },
  created () {
    this.fileURL = window.localStorage.getItem('fileURL')
    this.videoPlay()
    // 获取token
    this.$http.get('http://p.gerinn.com/shellvideo/index.php/shell/getVideoQiNiuUploadToken').then((response) => {
      console.log(response.data)
      if (response.data.ret === 1) {
        this.data = response.data.data
      }
    }).catch((error) => {
      console.log(error)
    });
  },
  methods: {
    videoPlay () {
      this.$nextTick(() => {
        document.querySelectorAll('video')[0].play()
        document.addEventListener('WeixinJSBridgeReady', () => {
          document.querySelectorAll('video')[0].play()
        })
      })
    },
    recode () {
      let files = this.globle.videoFile = event.target.files[0]
      this.ramdomName = this.data.uuid + files.name.match(/\.?[^.\/]+$/)
      this.videoSize = files.size
      let URL = window.URL || window.webkitURL
      this.fileURL = URL.createObjectURL(files)
      this.videoPlay()
    },
    uploadVideo () {
      let _this = this 
      let formData = new FormData()
      formData.append('file', this.globle.videoFile)
      formData.append('key',  this.ramdomName)
      formData.append('token', this.data.token)
      formData.append('accept', '')
      $.ajax({
          url: 'http://upload.qiniup.com',
          type: 'POST',
          data: formData,
          processData: false,
          contentType: false,
          xhr: function(){
            let myXhr = $.ajaxSettings.xhr()          
            if (myXhr.upload){
              _this.isShowUpOverlay = true
              myXhr.upload.addEventListener('progress',function(e) {
                // console.log(e);
                if (e.lengthComputable) {
                  var percent = e.loaded / e.total*100;
                  _this.upText = '视频上传：' + percent.toFixed(0) + "%"
                  console.log('视频上传：' + percent.toFixed(0) + "%")
                }
              }, false)
            }
            return myXhr
          },
          success: function(res) {
            // 上传成功
            console.log(res, '上传成功')
            _this.$http.get('http://p.gerinn.com/shellvideo/index.php/shell/encodeVideo/' + _this.data.filename).then((response) => {
              console.log(response.data)
              if (response.data.ret === 1) {
                _this.$http.get('http://p.gerinn.com/shellvideo/index.php/shell/insertVideo?vurl=' + window.encodeURIComponent('http://szp.h5ome.com/' + _this.data.filename)).then((response) => {
                  console.log(response.data)
                  if (response.data.ret === 1) {
                    _this.globle.myData = response.data.data
                    _this.upText = '上传成功'
                    setTimeout(() => {
                      _this.$router.push('/rank')
                    }, 1000)
                  }
                }).catch((error) => {
                  console.log(error)
                })
              }
            }).catch((error) => {
              console.log(error)
            });
          },
          error: function(res) {  
            // 上传失败
            console.log(res)
            _this.upText = '上传失败'
            setTimeout(() => {
              _this.isShowUpOverlay = false
              _this.upText = ''                        
            }, 2000)
          }
        })       
    }
  }
}
</script>

<style>
#upload-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
#upload-container .video-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 1rem;
  width: 7.8125rem;
  height: 12.984375rem;
  background: url(../../common/img/frame.png) no-repeat;
  background-size: 100% 100%;
  margin: auto;
}
#upload-container .full-page-slide-wrapper {
  position: absolute;
  z-index: 20;
  top: .8rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 6.5625rem;
  height: 11.6875rem;
  overflow: hidden;
}
#upload-container .full-page-wrapper {
  position: relative;
  left: 0;
  top: 0;
}
#upload-container .full-page-wrapper video {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 6.5625rem;
  height: 11.6875rem;
  object-fit: fill;
  z-index: 9;
}
#upload-container .f-btn {
  position: absolute;
  bottom: .6rem;
  width: 3.796875rem;
  height: 1.421875rem;
}
#upload-container .f-btn.recode-btn {
  left: 1rem;
}
#upload-container .video-file {
  position: absolute;
  bottom: .6rem;
  width: 3.796875rem;
  height: 1.421875rem;
  left: 1rem;
  opacity: 0;
  z-index: 999;
}
#upload-container .f-btn.upload-btn {
  right: 1rem;
}
#upload-container .tip-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
#upload-container .tip-wrapper .tip-content{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 8rem;
  height: 4.5rem;
  background: #063672;
  color: #fff;
  border: 1px solid #fff;
  border-radius: .4rem;
  z-index: 9999;
  overflow: hidden;
}
#upload-container .tip-wrapper .tip-content .close {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.875rem;
}
#upload-container .tip-wrapper .tip-content .tip-title {
  width: 3.4375rem;
  display: block;
  margin: auto;
  margin-top: .4rem;
}
#upload-container .tip-wrapper .tip-content p {
  text-align: center;
  font-size: .4rem;
  line-height: .6rem;
  margin-top: .6rem;
}
#upload-container .updata-overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 9999; 
  background: rgba(0, 0, 0, 0.8);       
}
#upload-container #showtip {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 6rem;
  height: 4rem;
  line-height: 4rem;
  color: #000;
  font-size: .45rem;
  background: #fff;
  margin: auto;
  border-radius: .3rem;
  text-align:center;
  font-family: 'HYHei';
}
</style>



