<template>
  <div id="watch-container">
    <div class="video-wrapper">
      <div class="full-page-slide-wrapper">
        <div class="slide" ref="slide">
          <div class="slide-group" ref="slideGroup">
            <div v-for="(item, index) in items" :key="item.id">
              <div class="full-page-wrapper">
                <video @click="palyVideo($event)" :src="item.vurl" loop playsinline webkit-playsinline x5-playsinline :poster="item.vurl + '.jpg'"></video>
                <div class="like-wrap" @click="likeBtn(item.vuuid, index)">
                  <span class="iconfont icon-heart heart-btn" :class="{active: item.liked == '1'}"></span>
                  <p>{{item.vlike}}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="back" @click="goRank()"><span>&lt;</span>&nbsp;&nbsp;返回</div>
    <div class="tip-overlay" v-show="isShowTip" @click="closeTip()"></div>
    <div class="ft-btn yaoqing-btn" @click="isShowSharetip = true"></div>
    <div class="ft-btn lookvideo-btn" @click="goMyvideo()"></div>
    <div class="share-overlay" v-show="isShowSharetip" @click="isShowSharetip = false">
      <img src="../../common/img/share-tip.png" class="share-tip">
    </div>
  </div>
</template>

<script>
import { addClass } from 'common/js/dom'
import BScroll from 'better-scroll'
export default {
  name: 'watch',
  data () {
    return {
      isShowTip: window.localStorage.getItem('flagwatch') ? false : true,
      isShowSharetip: false,
      items: this.globle.getUserInfo,
      currentPageIndex: 0,
      autoPlay: false,
      interval: 1000
    }
  },
  created () {
    console.log(this.globle.getUserInfo[this.$route.query.index])
  },
  mounted () {
    this.update()
    window.addEventListener('resize', () => {
      if (!this.slide || !this.slide.enabled) {
        return
      }
      clearTimeout(this.resizeTimer)
      this.resizeTimer = setTimeout(() => {
        if (this.slide.isInTransition) {
          this._onScrollEnd()
        } else {
          if (this.autoPlay) {
            this._play()
          }
        }
        this.refresh()
      }, 60)
    })
  },
  activated() {
    if (!this.slide) {
      return
    }
    this.slide.enable()
    let pageIndex = this.slide.getCurrentPage().pageY
    this.slide.goToPage(pageIndex, 0, 0)
    this.currentPageIndex = pageIndex
    if (this.autoPlay) {
      this._play()
    }
  },
  deactivated() {
    this.slide.disable()
    clearTimeout(this.timer)
  },
  beforeDestroy() {
    this.slide.disable()
    clearTimeout(this.timer)
  },
  methods: {
    update() {
      if (this.slide) {
        this.slide.destroy()
      }
      this.$nextTick(() => {
        this.init()
      })
    },
    refresh() {
      this._setSlideHeight(true)
      this.slide.refresh()
    },
    next() {
      this.slide.next()
    },
    init() {
      clearTimeout(this.timer)
      this.currentPageIndex = 0
      this._setSlideHeight()
      this._initSlide()

      if (this.autoPlay) {
        this._play()
      }
    },
    _setSlideHeight(isResize) {
      this.children = this.$refs.slideGroup.children

      let height = 0
      let slideHeight = this.$refs.slide.clientHeight
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        addClass(child, 'slide-item')

        child.style.height = slideHeight + 'px'
        height += slideHeight
      }
      if (this.loop && !isResize) {
        height += 2 * slideHeight
      }
      this.$refs.slideGroup.style.height = height + 'px'
    },
    _initSlide() {
      this.slide = new BScroll(this.$refs.slide, {
        scrollX: false,
        scrollY: true,
        momentum: false,
        snap: {
          loop: false,
          threshold: 0.3,
          speed: 400
          // easing: {
          //   style: 'cubic-bezier(0.175, 0.885, 0.32, 1.275)'
          // }
        },
        bounce: false,
        click: true
      })
      this.slide.goToPage(0, this.$route.query.index, 0)
      this.$nextTick(() => {
        document.querySelectorAll('video')[this.$route.query.index].play()
        document.addEventListener('WeixinJSBridgeReady', () => {
          for (let i = 0; i < document.querySelectorAll('video').length; i++) {
            document.querySelectorAll('video')[i].play()
            document.querySelectorAll('video')[i].pause()
          }
          document.querySelectorAll('video')[this.$route.query.index].play()
        })
      })

      this.slide.on('scrollEnd', this._onScrollEnd)

      this.slide.on('touchEnd', () => {
        if (this.autoPlay) {
          this._play()
        }
      })

      this.slide.on('beforeScrollStart', () => {
        if (this.autoPlay) {
          clearTimeout(this.timer)
        }
      })
    },
    _onScrollEnd() {
      let pageIndex = this.slide.getCurrentPage().pageY
      if (this.currentPageIndex > pageIndex) {
        this.currentPageIndex = pageIndex
        document.querySelectorAll('video')[this.currentPageIndex + 1].pause()
        // document.querySelectorAll('video')[this.currentPageIndex + 1].currentTime  = 0
        document.querySelectorAll('video')[this.currentPageIndex].play()
        // document.addEventListener('WeixinJSBridgeReady', () => {
        //   document.querySelectorAll('video')[this.currentPageIndex + 1].pause()
        //   document.querySelectorAll('video')[this.currentPageIndex].play()
        // })
      } else {
        this.currentPageIndex = pageIndex
        document.querySelectorAll('video')[this.currentPageIndex - 1].pause()
        // document.querySelectorAll('video')[this.currentPageIndex - 1].currentTime = 0
        document.querySelectorAll('video')[this.currentPageIndex].play()
        // document.addEventListener('WeixinJSBridgeReady', () => {
        //   document.querySelectorAll('video')[this.currentPageIndex - 1].pause()
        //   document.querySelectorAll('video')[this.currentPageIndex].play()
        // })
      }
      if (this.autoPlay) {
        this._play()
      }
    },
    _play() {
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.slide.next()
      }, this.interval)
    },
    palyVideo (event) {
      event.target.play()
    },
    closeTip () {
      window.localStorage.setItem('flagwatch', true)
      this.isShowTip = false
    },
    goRank () {
      this.$router.push('/rank')
    },
    likeBtn (vuuid, index) {
      this.$http.get('http://p.gerinn.com/shellvideo/index.php/shell/like?vuuid=' + vuuid).then((response) => {
        if (response.data.ret === 1) {
          this.items[index].liked = '1'
          this.items[index].vlike = parseInt(this.items[index].vlike) + 1
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    goMyvideo () {
      this.$router.push('/myvideo')      
    }    
  }
}
</script>

<style>
#watch-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
#watch-container .video-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 0.8rem;
  width: 7.8125rem;
  height: 12.984375rem;
  background: url(../../common/img/frame.png) no-repeat;
  background-size: 100% 100%;
  margin: auto;
}
#watch-container .full-page-slide-wrapper {
  position: absolute;
  z-index: 20;
  top: .8rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 6.5625rem;
  height: 11.6875rem;
  overflow: hidden;
}
#watch-container .full-page-wrapper {
  position: relative;
  left: 0;
  top: 0;
}
#watch-container .full-page-wrapper video {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 6.5625rem;
  height: 11.6875rem;
  object-fit: fill;
  z-index: 9;
}
#watch-container .full-page-wrapper .cover {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  z-index: 10;
}
#watch-container .full-page-slide-wrapper .slide {
  height: 100%;
  min-height: 1px;
}
#watch-container .full-page-slide-wrapper .slide .slide-group {
  height: 100%;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}
#watch-container .full-page-slide-wrapper .slide .slide-group .slide-item {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
#watch-container .full-page-slide-wrapper .slide .slide-group .slide-item .full-page-img-wrapper {
  position: relative;
  height: 100%;
  background-size: cover;
  transform: translateZ(0);
}
#watch-container .tip-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../../common/img/tipoverlay.png) no-repeat;
  background-size: 100% 100%;
  z-index: 9999;
}
#watch-container .ft-btn {
  position: absolute;
  bottom: .4rem;
  width: 4.0rem;
  height: 1.359375rem;  
}
#watch-container .ft-btn.yaoqing-btn {
  left: 1rem;
  background: url(../../common/img/yaoqing.png) no-repeat;
  background-size: 100% 100%
}
#watch-container .ft-btn.lookvideo-btn {
  right: 1rem;
  background: url(../../common/img/lookvideo.png) no-repeat;
  background-size: 100% 100%  
}
#watch-container .back {
  position: absolute;
  top: .4rem;
  left: 1rem;
  color: #fff;
  font-size: .4rem;
}
#watch-container .back span {
  font-size: .5rem;
  vertical-align: middle;
}
#watch-container .share-overlay{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(10, 28, 58, .6);
  z-index: 9999;
}
#watch-container .share-overlay .share-tip {
  width: 3.84375rem;
  position: absolute;
  top: .5rem;
  right: 1rem;
}
#watch-container .like-wrap {
  position: absolute;
  bottom: 2rem;
  right: 0;
  width: 1.2rem;
  height: 1.1rem;
  z-index: 9999;  
}
#watch-container .like-wrap p {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: .35rem;
}
#watch-container .heart-btn {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  color: #fff;
  font-size: .6rem;
  text-align: center;  
}
#watch-container .heart-btn.active {
  color: red;
}
</style>



