<template>
  <div id="home-container">
    <img src="./home-img.png" class="home-img">
    <img src="./start-btn.png" class="start-btn" @click="goSwap()">
    <div class="details-btn-wrap">
      <span class="details-btn" @click="isDetailShow = true">活动详情</span>
    </div>
    <div class="details-wrapper" v-show="isDetailShow">
      <div class="details-content">
        <img src="./details-title-img.png" class="details-title-img">
        <img src="./close.png" class="close" @click="isDetailShow = false">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      isDetailShow: false
    }
  },
  created () {
  },
  methods: {
    goSwap () {
      this.$router.push('/swap')
    }
  }
}
</script>

<style>
#home-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
}
#home-container .home-img {
  position: absolute;
  top: 1.2rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
}
#home-container .start-btn {
  position: absolute;
  bottom: 2rem;
  left: 0;
  right: 0;
  margin: auto;
  width: 3.921875rem;
}
#home-container .details-btn-wrap {
  position: absolute;
  bottom: 1rem;
  left: 0;
  right: 0;
  margin: auto;
  color: #fff;
  font-size: 0.40625rem;
  text-align: center;
}
#home-container .details-btn-wrap .details-btn {
  border-bottom: 0.015625rem solid #fff;
}
.details-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('/static/img/1 (50).jpg') no-repeat;
  background-size: cover;
}
.details-wrapper .details-content {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  z-index: 9999;
  background: red;
  width: 8.90625rem;
  height: 15.703125rem;
  border-radius: 0.5rem;
  background: rgba(3, 53, 117, .6);
}
.details-wrapper .details-content .details-title-img {
  width: 2.28125rem;
  display: block;
  margin: auto;
  margin-top: .6rem;
}
.details-wrapper .details-content  .close {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.875rem;
}
</style>



