import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'

import resource from 'common/js/resource'
import globle from 'common/js/globle'
import 'common/css/reset.css'

Vue.prototype.$http = axios
Vue.use(resource)
Vue.use(globle)

Vue.config.productionTip = false
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



